@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.light {
  background: #F5F5F5;
}

.dark {
  background: #272626;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
}

.sparkle-cursor {
  cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='40' height='48' viewport='0 0 100 100' style='fill:black;font-size:24px;'><text y='50%'>ðŸ‘€</text></svg>")
    16 0, auto;
}

h1,h4,p,a {
  text-decoration: none;
  font-family: 'Nunito', sans-serif;
  cursor: pointer;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 12px 32pm;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(248, 217, 15);
  color: #222;
  border: 1pm solid #fff;
  font-weight: 600;
  cursor: pointer
}

.btn-light {
  background: transparent;
  color: #fff;
}

.btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}

/* DESKTOP */
@media screen and (min-width: 900px) {
  .react-pdf__Document {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .react-pdf__Page {
    box-shadow: 0 30px 40px 0 rgba(16, 36, 94, 0.2);
    border-radius: 8px;
    position: relative;
    max-height: calc(100% - 10vw);
    min-width: 288px;
    border: 0;
    background: white;
    overflow: auto;
    font: inherit;
    color: inherit;
  }
}
/* MOBILE */
@media screen and (max-width: 900px) {
  .react-pdf__Document {
    visibility: hidden;
  }
  .react-pdf__Page {
    box-shadow: 0 20px 30px 0 rgba(16, 36, 94, 0.2);
    border-radius: 5px;
    position: relative;
    border: 0;
    background: white;
    font: inherit;
    color: inherit;
    width: -moz-fit-content;
    width: fit-content;
  }
}



.App {
  text-align: center;
}

.App.light {
  background: #fff;
}

.App.dark {
  background: #000;
}


.header.light {
    background-color: #F5F5F5;
}

.header.light a {
    color: #000;
}

.header.dark {
    background-color: #272626;
}

.header.dark a {
    color: #fff;
}

/* DESKTOP */
@media screen and (min-width: 900px) {
    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 3rem 3rem 0rem;
        margin-left: 7rem;
        margin-right: 7rem;
        margin-bottom: 3%;
    }
    .heading {
        display: flex;
        flex-direction: row;
    }
    .sparkle-icon {
        width: 20%;
    }
    .nav-menu {
        display: flex;
    }
}

/* MOBILE */
@media screen and (max-width: 900px) {
    .header {
        display: block;
        align-items: center;
        padding-top: 3rem;
    }
    .heading {
        display: block;
        align-items: center;
    }
    .sparkle-icon {
        visibility: hidden;
        width: 0%;
        height: 0%;
    }
    .nav-menu {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top: 1rem;
    }
}

.nav-menu li {
    padding: 0rem 1rem 0rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 600;
}
/* DESKTOP */
@media screen and (min-width: 1200px) {
    .intro-section {
        /* display side by side */
        display: flex;
        flex-direction: row;
        /* top | left and right | bottom */
        margin: 0rem 2rem 5rem;
    }
    .intro-image {
        width: 50%;
        height: 50%;
        display: flex;
        justify-content: end;
        margin: 3rem;
    }
    img {
        width: 50%;
    }
    .intro-text {
        width: 50%;
        text-align: left;
        margin-top: 3rem;
        margin-left: 0rem;
        margin-right: 6rem;
        margin-bottom: 2rem;
    
        padding-top: 2rem;
        padding-left: 1rem;
        padding-right: 8rem;
    }
    .intro-text p {
        font-size: 1.2rem;
        font-weight: 400;
    }
    .intro-text .social-icons {
        margin: 2rem 0 0;
    }
}

/* MOBILE */
@media screen and (max-width: 1200px) {
    .intro-section {
        display: block;
        align-items: center;
        margin-top: 2rem;
        margin-bottom: 2rem;
    }
    .intro-image {
        width: 100%;
    }
    img {
        width: 50%;
    }
    .intro-text {
        text-align: left;
    }
    .intro-text p {
        font-size: 1.2rem;
        font-weight: 200;
        margin: 2rem 2rem 0rem;
    }
    .intro-text .social-icons {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
    
}

.intro-text a {
    color: #000;
    padding: 1rem 1rem 0;
    font-size: 1.5rem;
}

.intro-text p {
    color: #000;
}
/* DESKTOP */
@media screen and (min-width: 900px) {
    .center-section {
        background: #66507F;
        /* top | left and right | bottom */
        margin: 5rem 10rem 5rem;
        padding: 5rem 10rem 3rem;
    }
    .center-section p {
        color: #fff;
        text-align: left;
        margin-top: 2rem;
        font-size: 1.2rem;
        font-weight: 400;
    }
    .my-projects-btn {
        margin-top: 2rem;
        color: #000;
    }
}

/* MOBILE */
@media screen and (max-width: 900px) {
    .center-section {
        background: #66507F;
        /* top | left and right | bottom */
        margin-top: 5rem;
    }
    .center-section p {
        color: #fff;
        text-align: left;
        margin: 2rem;
        font-size: 1.2rem;
        font-weight: 200;
    }
    .my-projects-btn {
        padding-bottom: 2rem;
        margin-top: 2rem;
        color: #000;
    }
}

.center-section h3 {
    color: #fff;
    font-size: 1.5rem;
    font-family: 'Space Mono', monospace;  
    padding-top: 1rem;
}

.center-section-text a {
    color: #F1AB42;
}

.center-section-text a:hover {
    text-decoration: underline;
}

.my-projects-btn button {
    background: #F2DCC2;
    border-radius: 0.6rem;
    height: 3rem;
    width: 8rem;
    border: none;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
    font-size: 1.1rem;
    font-weight: 700;
}



/* DESKTOP */
@media screen and (min-width: 900px) {
    .featured-section {
        font-size: 1.5rem;
        margin-bottom: 5rem;
    }
}

/* MOBILE */
@media screen and (max-width: 900px) {
    .featured-section {
        font-size: 1.5rem;
        margin-top: 3rem;
        margin-bottom: 3rem;
    }
    .projects-grid {
        display: block;
        justify-content: center;
    }
    
}

.featured-section .projects-grid .card-content p, h4, h6 {
    color: #000;
}

.featured-section .projects-grid .card-content h4 {
    font-family: 'Space Mono', monospace; 
    color: #000; 
    font-size: 1rem;
}

.featured-section p {
    font-size: 1rem;
}

.featured-section .tags {
    visibility: hidden;
}

/* external link icon */
.more-projects a {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: #000;
    text-decoration: underline;
}

.more-projects a:hover {
    color: #000;
}

i {
    margin-left: 0.5rem;
}
  
.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
   
  .box {
    position: relative;
    width: 60%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: #FFF9F0;
    border-radius: 10px;
    padding: 20px;
    border: 1px solid #999;
    overflow: auto;
    display: flex;
    flex-direction: row;
  }
   
  .close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(15% - 30px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
  }

.box img {
    display: block;
    margin: auto;
    width: 100%;
}


/* DESKTOP */
@media screen and (min-width: 900px) {
    .card-content {
        width: 30%;
        margin: 1rem 1rem 1rem 1rem;
        display: block;
        padding: 2rem 2rem 2rem;
        /*border: 2px solid #999;*/
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        border-radius: 5px;
        overflow-wrap: normal;
    }
}

/* MOBILE */
@media screen and (max-width: 900px) {
    .card-content {
        width: 100%;
        display: block;
        padding: 1rem 1rem 1rem;
        margin: 1rem 0 1rem;
        /*border: 2px solid #999;*/
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        border-radius: 5px;
        overflow-wrap: normal;
    }
}

.card-content img {
    width: 100%;
    border-radius: 5px;
}

.card-content h4 {
    margin: 0.5rem 0 0;
    font-family: 'Space Mono', monospace;
    font-size: 1rem;
}

.card-content p  {
    width: 100%;
    text-align: left;
    margin: 0.5rem 0 0;
}

.projects-grid .card-content p, h4 {
    color: #fff;
}

.card-content a {
    color: #815AB4;
}

.card-content a:hover {
    text-decoration: underline;
}

.tag {
    display: flex;
    width: -moz-fit-content;
    width: fit-content;
    float: left;
    padding: 0.1rem 0.4rem 0.1rem;
    margin: 0.2rem;
}

.tags {
    margin-top: 1rem;
    float: bottom;
}
/* DESKTOP */
@media screen and (min-width: 900px) {
    .projects-intro h3 {
        color: #fff;
        font-weight: 400;
        text-align: left;
        margin-left: 6%;
    }
    .drop-down-menu {
        width: 50%;
        margin-left: 5%;
        margin-top: 1rem;
        margin-bottom: auto;
    }
    
}

/* MOBILE */
@media screen and (max-width: 900px) {
    .projects-intro h3 {
        color: #fff;
        font-weight: 300;
        text-align: center;
        margin-left: 6%;
        margin-top: 3rem;
    }
    .drop-down-menu {
        margin: 2rem 1rem 0;
    }
    
}

.card-content h4 {
    margin: 0.5rem 0 0;
    color: #fff;
}

.card-content p  {
    color: #fff;
    width: 100%;
    text-align: left;
    margin: 0.5rem 0 0;
}

.projects-grid {
    display: flex;
    flex-direction: row;
    overflow: auto;
    flex-wrap: wrap;
    margin: 3rem 3rem 3rem;
}
.footer {
    padding-top: 5%;
    padding-bottom: 2%;
}

.footer p {
    color: #5B5B5B;
}
